import Vue from 'vue';
import VueRouter from 'vue-router';

import HomeView from '@/views/Home/HomeView.vue';
import RecommentView from '@/views/Home/childView/RecommentView.vue';
import NavModuleView from '@/views/Home/childView/NavModuleView.vue';

import ConcreteType from '@/views/Home/childView/ConcreteType.vue';
import MoreView from '@/views/Home/childView/MoreView.vue';
import SoResult from '@/views/Home/childView/SoResult.vue';
import TingView from '@/views/Home/childView/TingView.vue';

import TopView from '@/views/Home/childView/TopView.vue';

import AlbumView from '@/views/AlbumView.vue';
import SearchView from '@/views/SearchView.vue';
import SoundView from '@/views/SoundView.vue';
import ZhuBoView from '@/views/ZhuBoView.vue';
import TingDanContentView from '@/views/TingDanContentView.vue';

Vue.use(VueRouter);

const routes = [
  {
    path: '',
    components: {
      'appPage': HomeView
    },
    children: [
      {
        path: '',
        components: { 'HomeChildExit': RecommentView }
      },
      {
        path: '/NavModule/:moduleKey',
        components: { 'HomeChildExit': NavModuleView }
      },
      {
        path: '/ConcreteType',
        components: { 'HomeChildExit': ConcreteType }
      },
      {
        path: '/MoreView',
        components: { 'HomeChildExit': MoreView }
      },
      {
        path: '/SoResult',
        components: { 'HomeChildExit': SoResult }
      },
      // 
      {
        path: '/ting/:module',
        components: { 'HomeChildExit': TingView },
      },
      {
        path: '/top',
        components: { 'HomeChildExit': TopView }
      }
    ]
  },
  {
    path: '/AlbumView',
    components: {
      'appPage': AlbumView
    }
  },
  {
    path: '/SearchView',
    components: {
      'appPage': SearchView
    }
  },
  {
    path: '/SoundView',
    components: {
      'appPage': SoundView
    }
  },
  {
    path: '/ZhuBoView',
    components: {
      'appPage': ZhuBoView
    }
  },
  {
    path: '/TingDanContent',
    components: {
      'appPage': TingDanContentView
    }
  }
];

const router = new VueRouter({
  routes
});

export default router;
