<template>
    <div class="AlbumView">
        <header>
            <div class="cover">
            </div>
            <div class="left">
                <img :src="getCoverUrl" class="athor">
                <img src="../../public/img/listen_pic_album@3x_d18f4f7.png">
                <div class="buttom">
                    <svg t="1665853623375" class="icon" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="1513" width="128" height="128">
                        <path
                            d="M896 426.666667h-85.333333V341.333333a298.666667 298.666667 0 1 0-597.333334 0v85.333334H128A85.426667 85.426667 0 0 0 42.666667 512v170.666667a85.426667 85.426667 0 0 0 85.333333 85.333333h106.666667a21.333333 21.333333 0 0 0 21.333333-21.333333V341.333333c0-141.16 114.84-256 256-256s256 114.84 256 256v384c0 94.106667-76.56 170.666667-170.666667 170.666667h-3.66a64.106667 64.106667 0 0 0-60.34-42.666667h-42.666666a64 64 0 0 0 0 128h42.666666a64.106667 64.106667 0 0 0 60.34-42.666666H597.333333a213.506667 213.506667 0 0 0 209.086667-170.666667H896a85.426667 85.426667 0 0 0 85.333333-85.333333V512a85.426667 85.426667 0 0 0-85.333333-85.333333z"
                            fill="#ffffff" p-id="1514"></path>
                    </svg>
                    <span class="content">{{parseInt(chooseAlbum.backNum/10000000) +''}}</span>
                </div>
            </div>
            <div class="right">
                <p class="title">{{chooseAlbum.topName}}</p>
                <div class="author">
                    <img :src="getAnchorLogo">
                    <span class="name">{{getAnchorNickname}}</span>
                </div>
            </div>
        </header>

        <div class="other" v-html="otherHtml"></div>

        <div class="JieMu">
            <header>
                <span>({{lookingYinGuiObj.maxYinGuiNum}})</span>
            </header>
            <section class="box" @scroll="boxScrollMethod()">
                <div class="scrollContent">
                    <YinGuiItemComponent v-for="(item,index) in lookingYinGuiObj.YinGuiList" :YinGuiItem="item"
                        :key="index">
                    </YinGuiItemComponent>
                    <div class="loading" v-if="isLoading">
                        <img src="../../public/img/GMCoOSMF-28lAAAljwEkktf6.gif">
                        <p>...</p>
                    </div>
                    <div class="isFinish" v-if="isFinish"></div>
                </div>
            </section>
        </div>

        <ZhuboMsgComponent :anchorInfo="chooseAlbum.anchorInfo"></ZhuboMsgComponent>

        <XiangSiAlbumsComponent></XiangSiAlbumsComponent>

        <modulesComponent :module="JinDianBiTingModule"></modulesComponent>
    </div>
</template>

<script>
const axios = require('axios').default;
const lodash = require('lodash');

import YinGuiItemComponent from '@/components/YinGuiItemComponent.vue';
import modulesComponent from '@/components/modulesComponent.vue';
import ZhuboMsgComponent from '@/components/ZhuboMsgComponent.vue';
import XiangSiAlbumsComponent from '@/components/XiangSiAlbumsComponent.vue';
export default {
    name: 'AlbumView',
    data: function () {
        return {
            otherHtml: '',
            isLoading: true,
            isFinish: false,
            JinDianBiTingModule: { title: '', albums: [] },
            anchorId: '',
        };
    },
    components: {
        YinGuiItemComponent,
        modulesComponent,
        ZhuboMsgComponent,
        XiangSiAlbumsComponent
    },
    computed: {
        chooseAlbum: {
            set: function (value) {
                this.$store.commit({ type: 'setChooseAlbum', value: value });
            },
            get: function () {
                return this.$store.state.chooseAlbum;
            }
        },
        getCoverUrl: {
            get: function () {
                if (this.chooseAlbum.coverUrl.includes("http")) {
                    return this.chooseAlbum.coverUrl;
                } else {
                    return "http://imagev2.xmcdn.com/" + this.chooseAlbum.coverUrl;
                }
            }
        },
        getAnchorLogo: {
            get: function () {
                return this.chooseAlbum.anchorInfo ? 'http://imagev2.xmcdn.com/' + this.chooseAlbum.anchorInfo.userInfo.logo : '';
            }
        },
        getAnchorNickname: function () {
            return this.chooseAlbum.anchorInfo ? this.chooseAlbum.anchorInfo.userInfo.nickname : '';
        },
        lookingYinGuiObj: {
            get: function () {
                return this.$store.state.lookingYinGuiObj;
            },
            set: function (value) {
                this.$store.commit({ type: 'setLookingYinGuiObj', value: value });
            }
        },
        controlsParams: {
            get: function () {
                return this.$store.state.controlsParams;
            },
            set: function (value) {
                this.$store.commit({ type: 'setControlsParams', value: value });
            }
        }
    },
    methods: {
        // 
        boxScrollMethod: function () {
            let box = document.querySelector('.JieMu>.box');
            if (parseInt(box.scrollHeight - box.scrollTop) <= box.clientHeight) {
                axios.get('https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage',
                    { params: { albumId: this.chooseAlbum.id, page: ++this.lookingYinGuiObj.nowYinGuiPage, pageSize: 10, asc: true, countKeys: 'play,comment' } })
                    .then((data) => {
                        let trackDetailInfos = data.data.data.trackDetailInfos;
                        let copyLookingYinGuiObj = lodash.cloneDeep(this.lookingYinGuiObj);
                        trackDetailInfos.forEach((v) => {
                            // 
                            copyLookingYinGuiObj.YinGuiList.push({
                                id: v.id,
                                title: v.trackInfo.title,
                                cover: v.trackInfo.cover,
                                duration: v.trackInfo.duration,
                                playPath: v.trackInfo.playPath,
                                currentRate: 0
                            });
                            this.lookingYinGuiObj = copyLookingYinGuiObj;
                        });

                        this.isLoading = false;

                        if (trackDetailInfos.length <= 0) {
                            this.isLoading = false;
                            this.isFinish = true;
                        }
                    });
                this.isLoading = false;
            }
        }
    },
    created: function () {
        this.isLoading = true;
        this.lookingYinGuiObj = {
            YinGuiList: [],
            nowYinGuiPage: 1,
            maxYinGuiNum: 0
        };
        axios.get('https://apis.netstart.cn/ximalaya/album/queryAlbumPage/' + this.$route.query.id)
            .then((data) => {
                this.otherHtml = data.data.data.albumRichInfo.richIntro;
                return axios.get('https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage',
                    { params: { albumId: this.chooseAlbum.id, page: this.lookingYinGuiObj.nowYinGuiPage, pageSize: 10, asc: true, countKeys: 'play,comment' } });
            })
            .then(((data) => {
                let copyLookingYinGuiObj = lodash.cloneDeep(this.lookingYinGuiObj);
                copyLookingYinGuiObj.albumId = this.$route.query.id;
                copyLookingYinGuiObj.albumTitle = this.chooseAlbum.topName;
                copyLookingYinGuiObj.albumCover = this.chooseAlbum.coverUrl;
                copyLookingYinGuiObj.maxYinGuiNum = data.data.data.totalCount;

                let trackDetailInfos = data.data.data.trackDetailInfos;
                trackDetailInfos.forEach((v) => {
                    // 
                    copyLookingYinGuiObj.YinGuiList.push({
                        id: v.id,
                        title: v.trackInfo.title,
                        cover: v.trackInfo.cover,
                        duration: v.trackInfo.duration,
                        playPath: v.trackInfo.playPath,
                        currentRate: 0
                    });
                });
                this.lookingYinGuiObj = copyLookingYinGuiObj;
                this.isLoading = false;

                return axios.get('https://apis.netstart.cn/ximalaya/album/queryClassicListenAlbums');
            }))
            .then((data) => {
                data.data.data.forEach((v) => {
                    this.JinDianBiTingModule.albums.push({
                        id: v.id,
                        coverUrl: v.albumInfo.cover,
                        topName: v.albumInfo.title,
                        center: v.albumInfo.customTitle,
                        prevNum: v.statCountInfo.trackCount,
                        backNum: v.statCountInfo.playCount,
                        anchorInfo: null
                    });
                });

                return axios.get('https://apis.netstart.cn/ximalaya/album/queryAlbumPage/' + this.$route.query.id);
            })
            .then((data) => {
                this.anchorId = data.data.data.anchorId;

                return axios.get('https://apis.netstart.cn/ximalaya/anchor/queryUserInfo/' + this.anchorId);
            })
            .then((data) => {
                let copyChooseAlbum = lodash.cloneDeep(this.chooseAlbum);
                copyChooseAlbum.anchorInfo = lodash.cloneDeep(data.data.data);
                this.chooseAlbum = copyChooseAlbum;
                localStorage.setItem('chooseAlbum', JSON.stringify(this.chooseAlbum));
            });
    },
    watch: {
        $route: {
            handler: function (to) {
                this.otherHtml = '';
                this.lookingYinGuiObj = {
                    YinGuiList: [],
                    nowYinGuiPage: 1,
                    maxYinGuiNum: 0
                };
                this.isLoading = false;
                this.isFinish = false;
                this.JinDianBiTingModule = { title: '', albums: [] };
                this.anchorId = '';

                this.isLoading = true;
                axios.get('https://apis.netstart.cn/ximalaya/album/queryAlbumPage/' + to.query.id)
                    .then((data) => {
                        this.otherHtml = data.data.data.albumRichInfo.richIntro;
                        return axios.get('https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage',
                            { params: { albumId: this.chooseAlbum.id, page: this.lookingYinGuiObj.nowYinGuiPage, pageSize: 10, asc: true, countKeys: 'play,comment' } });
                    })
                    .then(((data) => {
                        let copyLookingYinGuiObj = lodash.cloneDeep(this.lookingYinGuiObj);
                        copyLookingYinGuiObj.albumId = this.$route.query.id;
                        copyLookingYinGuiObj.albumTitle = this.chooseAlbum.topName;
                        copyLookingYinGuiObj.albumCover = this.chooseAlbum.coverUrl;
                        copyLookingYinGuiObj.maxYinGuiNum = data.data.data.totalCount;

                        let trackDetailInfos = data.data.data.trackDetailInfos;
                        trackDetailInfos.forEach((v) => {
                            // 
                            copyLookingYinGuiObj.YinGuiList.push({
                                id: v.id,
                                title: v.trackInfo.title,
                                cover: v.trackInfo.cover,
                                duration: v.trackInfo.duration,
                                playPath: v.trackInfo.playPath,
                                currentRate: 0,
                            });
                        });
                        this.lookingYinGuiObj = copyLookingYinGuiObj;
                        this.isLoading = false;

                        return axios.get('https://apis.netstart.cn/ximalaya/album/queryClassicListenAlbums');
                    }))
                    .then((data) => {
                        data.data.data.forEach((v) => {
                            this.JinDianBiTingModule.albums.push({
                                id: v.id,
                                coverUrl: v.albumInfo.cover,
                                topName: v.albumInfo.title,
                                center: v.albumInfo.customTitle,
                                prevNum: v.statCountInfo.trackCount,
                                backNum: v.statCountInfo.playCount,
                                anchorInfo: null
                            });
                        });

                        return axios.get('https://apis.netstart.cn/ximalaya/album/queryAlbumPage/' + to.query.id);
                    })
                    .then((data) => {
                        this.anchorId = data.data.data.anchorId;

                        return axios.get('https://apis.netstart.cn/ximalaya/anchor/queryUserInfo/' + this.anchorId);
                    })
                    .then((data) => {
                        let copyChooseAlbum = lodash.cloneDeep(this.chooseAlbum);
                        copyChooseAlbum.anchorInfo = lodash.cloneDeep(data.data.data);
                        this.chooseAlbum = copyChooseAlbum;
                        localStorage.setItem('chooseAlbum', JSON.stringify(this.chooseAlbum));
                    });
            }
        }
    },
    mounted: function () {
        this.controlsParams.isShowControls = true;
    }
}
</script>

<style lang="less">
.AlbumView {
    font-size: 12px;

    >header {
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 50rem 0;
        background-size: cover;
        background-repeat: no-repeat;
        position: relative;
        overflow: hidden;

        >.cover {
            position: absolute;
            top: 0;
            left: 0;
            background-color: #29303a;
            width: 100%;
            height: 100%;
            z-index: -1;
        }

        >.left {
            display: flex;
            position: relative;

            >img {
                display: block;
                height: 90rem;
            }

            >.buttom {
                position: absolute;
                color: #fff;
                display: flex;
                align-items: center;
                bottom: 2rem;
                font-weight: bold;

                >.icon {
                    width: 18rem;
                    height: $width;
                }
            }
        }

        >.right {
            width: 60%;

            >.title {
                font-size: 18px;
                font-weight: bold;
                color: #fff;
                margin-bottom: 5rem;
                width: 100%;
                overflow: hidden;
                text-overflow: ellipsis;
                display: -webkit-box;
                -webkit-line-clamp: 2;
                -webkit-box-orient: vertical;
            }

            >.author {
                display: flex;
                align-items: center;

                img {
                    display: block;
                    width: 30rem;
                    border-radius: 50%;
                    margin-right: 5rem;
                }

                >.name {
                    color: rgba(236, 225, 225, 0.814);
                    font-size: 14px;
                }
            }
        }

    }

    >.other {
        width: 90%;
        margin: 10rem auto 0 auto;
        max-height: 450rem;
        overflow: auto;

        img {
            width: 100%;
        }
    }

    .JieMu {
        >header {
            padding: 20rem;

            span {
                font-weight: bold;
                font-size: 20px;
            }
        }

        >.box {
            max-height: 500rem;
            overflow: auto;

            >.scrollContent {
                >.loading {
                    padding: 20rem 0;

                    p {
                        font-size: 10px;
                        font-weight: bold;
                        text-align: center;
                    }

                    img {
                        display: block;
                        width: 10%;
                        margin: 0 auto;
                    }
                }

                >.isFinish {
                    font-size: 10px;
                    font-weight: bold;
                    text-align: center;
                    padding: 10rem 0;
                }
            }
        }
    }
}
</style>