<template>
    <div class="AlbumComponent" @click="AlbumComponentClick(albumMsg.id)">
        <div class="left">
            <img :src='getCoverUrl'>
        </div>
        <div class="right">
            <p class="topName">{{albumMsg.topName}}</p>
            <p class="center">{{albumMsg.center}}</p>
            <p class="buttom">
                <span class="prev">
                    <svg t="1665821306384" class="icon" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="6603" width="128" height="128">
                        <path
                            d="M64 640a21.333333 21.333333 0 0 1-21.333333-21.333333v-213.333334a21.333333 21.333333 0 0 1 42.666666 0v213.333334a21.333333 21.333333 0 0 1-21.333333 21.333333zM192 810.666667a21.333333 21.333333 0 0 1-21.333333-21.333334v-554.666666a21.333333 21.333333 0 0 1 42.666666 0v554.666666a21.333333 21.333333 0 0 1-21.333333 21.333334zM320 981.333333a21.333333 21.333333 0 0 1-21.333333-21.333333v-896a21.333333 21.333333 0 0 1 42.666666 0v896a21.333333 21.333333 0 0 1-21.333333 21.333333zM448 938.666667a21.333333 21.333333 0 0 1-21.333333-21.333334v-810.666666a21.333333 21.333333 0 0 1 42.666666 0v810.666666a21.333333 21.333333 0 0 1-21.333333 21.333334zM576 768a21.333333 21.333333 0 0 1-21.333333-21.333333v-469.333334a21.333333 21.333333 0 0 1 42.666666 0v469.333334a21.333333 21.333333 0 0 1-21.333333 21.333333zM704 938.666667a21.333333 21.333333 0 0 1-21.333333-21.333334v-810.666666a21.333333 21.333333 0 0 1 42.666666 0v810.666666a21.333333 21.333333 0 0 1-21.333333 21.333334zM832 768a21.333333 21.333333 0 0 1-21.333333-21.333333v-469.333334a21.333333 21.333333 0 0 1 42.666666 0v469.333334a21.333333 21.333333 0 0 1-21.333333 21.333333zM960 640a21.333333 21.333333 0 0 1-21.333333-21.333333v-213.333334a21.333333 21.333333 0 0 1 42.666666 0v213.333334a21.333333 21.333333 0 0 1-21.333333 21.333333z"
                            fill="#707070" p-id="6604"></path>
                    </svg>
                    <span class="content">{{albumMsg.prevNum}}</span>
                </span>
                <span class="back">
                    <svg t="1665821363224" class="icon" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="8635" width="128" height="128">
                        <path
                            d="M896 426.666667h-85.333333V341.333333a298.666667 298.666667 0 1 0-597.333334 0v85.333334H128A85.426667 85.426667 0 0 0 42.666667 512v170.666667a85.426667 85.426667 0 0 0 85.333333 85.333333h106.666667a21.333333 21.333333 0 0 0 21.333333-21.333333V341.333333c0-141.16 114.84-256 256-256s256 114.84 256 256v384c0 94.106667-76.56 170.666667-170.666667 170.666667h-3.66a64.106667 64.106667 0 0 0-60.34-42.666667h-42.666666a64 64 0 0 0 0 128h42.666666a64.106667 64.106667 0 0 0 60.34-42.666666H597.333333a213.506667 213.506667 0 0 0 209.086667-170.666667H896a85.426667 85.426667 0 0 0 85.333333-85.333333V512a85.426667 85.426667 0 0 0-85.333333-85.333333z"
                            fill="#515151" p-id="8636"></path>
                    </svg>
                    <span class="content">{{Math.round(albumMsg.backNum/10000) +''}}</span>
                </span>
            </p>
        </div>
    </div>
</template>

<script>
const lodash = require('lodash');
export default {
    name: 'AlbumComponent',
    props: { albumMsg: Object },
    computed: {
        chooseAlbum: {
            set: function (value) {
                this.$store.commit({ type: 'setChooseAlbum', value: value });
            },
            get: function () {
                return this.$store.state.chooseAlbum;
            }
        },
        getCoverUrl: {
            get: function () {
                if (this.albumMsg.coverUrl.includes("http")) {
                    return this.albumMsg.coverUrl;
                } else {
                    return "http://imagev2.xmcdn.com/" + this.albumMsg.coverUrl;
                }
            }
        }
    },
    methods: {
        AlbumComponentClick: function (id) {
            if (this.$route.fullPath != `/AlbumView?id=${id}`) {
                this.$router.push({ path: '/AlbumView', query: { id: id } });
            }
            let copyAlbumMsg = lodash.cloneDeep(this.albumMsg);
            this.chooseAlbum = copyAlbumMsg;
            localStorage.setItem('chooseAlbum', JSON.stringify(this.chooseAlbum));
        }
    }
}
</script>

<style lang="less">
.AlbumComponent {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10rem 0;
    border-bottom: 0.3px solid #f5f6f6;

    >.left {
        >img {
            display: block;
            width: 75rem;
            border-radius: 5px;
        }
    }

    >.right {
        width: 70%;

        >.topName {
            font-size: 16px;
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }

        >.center {
            font-size: 12px;
            color: rgba(128, 128, 128, 0.482);
            width: 100%;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            margin: 3rem 0;
        }

        >.buttom {

            >.prev,
            >.back {
                margin: 0 5rem;

                >.content {
                    color: gray;
                    font-size: 14px;
                }
            }

            >.prev .icon,
            >.back .icon {
                width: 14rem;
                height: 14rem;
                margin-right: 5rem;
            }
        }
    }
}
</style>