<template>
    <div class="RecommentView">
        <SwiperComponent :swipeImg="swipeImg"></SwiperComponent>
        <TomatosComponent :tomatoes="tomatoes"></TomatosComponent>
        <modulesComponent v-for="(item,index) in modules" :key="index" :module="item"></modulesComponent>
        <MoreTuijianComponent :module="moreTuijianModule"></MoreTuijianComponent>
    </div>
</template>

<script>
const axios = require('axios').default;
const lodash = require('lodash');

import SwiperComponent from '@/components/SwiperComponent.vue';
import TomatosComponent from '@/components/TomatosComponent.vue';
import modulesComponent from '@/components/modulesComponent.vue';
import MoreTuijianComponent from '@/components/MoreTuijianComponent.vue';

export default {
    name: 'RecommentView',
    data: function () {
        return {
            swipeImg: [],
            tomatoes: [],
            modules: [],
            moreTuijianModule: {
                albums: []
            }
        };
    },
    computed: {
        chooseTobModule: {
            get: function () {
                return this.$store.state.chooseTobModule;
            }
        }
    },
    components: {
        SwiperComponent,
        TomatosComponent,
        modulesComponent,
        MoreTuijianComponent

    },
    created: function () {
        axios.get('https://apis.netstart.cn/ximalaya/index/queryIndexTabContent')
            .then((data) => {
                data.data.data.moduleContent.focusImages.forEach((v) => {
                    this.swipeImg.push(v.adFocusPictureDetail.cover);
                });
                data.data.data.moduleContent.tomatoes.forEach((v) => {
                    let item = lodash.cloneDeep(v);
                    this.tomatoes.push(item);
                });

                data.data.data.moduleContent.moduleRankDatas.forEach((v) => {
                    let module = { title: v.title, albums: [] };
                    v.rankingContentInfoList.rankModuleInfoList.forEach((v_1) => {
                        module.albums.push({
                            id: v_1.id,
                            coverUrl: v_1.albumInfo.cover,
                            topName: v_1.albumInfo.title,
                            center: v_1.albumInfo.customTitle,
                            prevNum: v_1.statCountInfo.trackCount,
                            backNum: v_1.statCountInfo.playCount,
                            anchorInfo: null
                        });
                    });
                    this.modules.push(module);
                });
                return axios.get('https://apis.netstart.cn/ximalaya/index/queryCategoryFeed', { params: { moduleKey: this.chooseTobModule.moduleKey } });
            })
            .then(((data) => {
                let materials = data.data.data.materials;
                if (materials) {
                    let _this = this;
                    materials.forEach((v) => {
                        if (v.data.trackInfo) {
                            _this.moreTuijianModule.albums.push({
                                id: v.data.trackInfo.albumId,
                                coverUrl: v.data.trackInfo.cover,
                                topName: v.data.trackInfo.albumTitle,
                                center: v.data.trackInfo.title,
                                prevNum: '',
                                backNum: v.data.statCountInfo.playCount,
                                anchorInfo: null
                            });
                        }
                    });
                }
            }));
    }
}
</script>

<style lang="less">
.RecommentView {}
</style>