<template>
    <div class="TingDanContentView">
        <header>
            <p class="title">
                {{title}}
            </p>
            <section class="authorSection">
                <div class="left">
                    <img :src="avatarImg">
                </div>
                <div class="right">
                    <p class="nickName">
                        {{authorMsg.authorNickName}}</p>
                    <p class="buttom">
                        <span class="releaseAt">{{releaseAt}}</span>
                    </p>
                </div>
            </section>
        </header>
        <section class="Data" v-for="(item,index) in datasType" :key="index">
            <section class="content" v-html="item.richIntro"></section>
            <section class="Albums" v-for="(album,i) in item.albums" :key="i">
                <TingDanAlbum :albumMsg="album"></TingDanAlbum>
            </section>
        </section>
    </div>
</template>

<script>
const axios = require('axios').default;
import TingDanAlbum from '@/components/TingDanAlbum.vue';
export default {
    name: 'TingDanContentView',
    data: function () {
        return {
            title: '',
            contentType: '',
            authorMsg: {
                authorId: '',
                authorNickName: '',
                authorAvatar: ''
            },
            releaseAt: '',
            datasType: []
        };
    },
    computed: {
        avatarImg: {
            get: function () {
                return this.authorMsg.authorAvatar.includes('http') ? this.authorMsg.authorAvatar : 'http://imagev2.xmcdn.com/' + this.authorMsg.authorAvatar;
            }
        },
        controlsParams: {
            get: function () {
                return this.$store.state.controlsParams;
            },
            set: function (value) {
                this.$store.commit({ type: 'setControlsParams', value: value });
            }
        }
    },
    components: {
        TingDanAlbum
    },
    created: function () {
        let _this = this;
        axios.get(`https://apis.netstart.cn/ximalaya/ting/querySpecialDetail/${this.$route.query.id}`)
            .then((data) => {
                let Data = data.data.data;
                this.title = Data.title;
                this.contentType = Data.contentType;
                this.authorMsg.authorId = Data.authorId;
                this.authorMsg.authorNickName = Data.authorNickName;
                this.authorMsg.authorAvatar = Data.authorAvatar;
                this.releaseAt = Data.releaseAt;

                let datas = Data.datas;
                datas.forEach((v) => {
                    let data = { richIntro: v.richIntro, albums: [] };
                    if (_this.contentType == 2) {
                        v.tracks.forEach((album) => {
                            data.albums.push({
                                id: album.id,
                                coverUrl: album.coverPath,
                                topName: album.albumTitle,
                                center: album.title,
                                prevNum: 0,
                                backNum: album.statCountInfo.playCount,
                                anchorInfo: null
                            });
                        });
                    } else {
                        v.albums.forEach((album) => {
                            data.albums.push({
                                id: album.albumInfo.id,
                                coverUrl: album.albumInfo.coverPath,
                                topName: album.albumInfo.title,
                                center: album.albumInfo.customTitle,
                                prevNum: album.statCountInfo.trackCount,
                                backNum: album.statCountInfo.playCount,
                                anchorInfo: null
                            });
                        });
                    }
                    this.datasType.push(data);
                });
            });
    },
    mounted: function () {
        this.controlsParams.isShowControls = true;
    }
};
</script>

<style lang="less">
.TingDanContentView {
    background-color: #f0f0f0;

    >header {
        width: 100%;

        >.title {
            font-size: 22px;
            font-weight: bold;
            padding: 20rem 0;
            width: 90%;
            white-space: pre-wrap;
            margin: 0 auto;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }

        >.authorSection {
            padding: 10rem;
            width: 90%;
            background-color: #fff;
            margin: 0 auto;
            display: flex;

            >.left {
                width: 10%;

                img {
                    width: 100%;
                    display: block;
                    border-radius: 50%;
                }
            }

            >.right {
                margin-left: 10rem;
                width: 80%;

                >.nickName {
                    font-size: 16px;
                    width: 100%;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }

                >.buttom {
                    color: gray;
                }
            }
        }
    }

    >.Data {
        width: 90%;
        margin: 0 auto;

        >.Albums {
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        img {
            display: block;
            width: 100%;
        }
    }
}
</style>