<script>
export default {
    name: 'SoundView',
    data() {
        return {
            show: false
        };
    },
    methods: {
        onChange(value) {
            let audio = document.querySelector('#app>audio');
            audio.currentTime = value;
        },
        isPlayClick: function () {
            let controlsParams = this.controlsParams;
            controlsParams.isPaly = !controlsParams.isPaly;
            this.controlsParams = controlsParams;
            localStorage.setItem('controlsParams', JSON.stringify(this.controlsParams));

            let audio = document.querySelector('#app>audio');
            if (this.controlsParams.isPaly) {
                setTimeout(() => {
                    if (this.controlsParams.currentRate == 0) {
                        audio.play();
                    } else {
                        audio.currentTime = this.controlsParams.currentRate;
                        audio.play();
                    }
                }, 500);
            } else {
                audio.pause();
            }
        },
        playListClick: function () {
            this.isShowPlayList = !this.isShowPlayList;
        },
        isCollectClick: function () {
            if (this.isCollection) {
                let newArr = this.myCollect.filter((v) => {
                    return v.albumId != this.playingYinGuiObj.albumId;
                });
                this.myCollect = newArr;
            } else {
                this.myCollect.unshift(this.playingYinGuiObj);
                this.showNotify();
            }
            localStorage.setItem('myCollect', JSON.stringify(this.myCollect));
        },
        showNotify() {
            this.show = true;
            setTimeout(() => {
                this.show = false;
            }, 2000);
        }
    },
    computed: {
        controlsParams: {
            get: function () {
                return this.$store.state.controlsParams;
            },
            set: function (value) {
                this.$store.commit({ type: 'setControlsParams', value: value });
            }
        },
        getCover: {
            get: function () {
                let cover = this.controlsParams.cover;
                return cover.includes('http') ? cover : 'http://imagev2.xmcdn.com/' + cover;
            }
        },
        getNowPlayingTime: {
            get: function () {
                let currentRate = this.controlsParams.currentRate;
                let mNum = currentRate / 60;
                let mm = parseInt(mNum) < 10 ? '0' + parseInt(mNum) : parseInt(mNum);
                let sNum = (mNum - parseInt(mNum)) * 60;
                let ss = parseInt(sNum) < 10 ? '0' + parseInt(sNum) : parseInt(sNum);
                return mm + ':' + ss;
            }
        },
        getRateTime: {
            get: function () {
                let rate = this.controlsParams.rate;
                let mNum = rate / 60;
                let mm = parseInt(mNum) < 10 ? '0' + parseInt(mNum) : parseInt(mNum);
                let sNum = (mNum - parseInt(mNum)) * 60;
                let ss = parseInt(sNum) < 10 ? '0' + parseInt(sNum) : parseInt(sNum);
                return mm + ':' + ss;
            }
        },
        isShowPlayList: {
            get: function () {
                return this.$store.state.isShowPlayList;
            },
            set: function (value) {
                this.$store.commit({ type: 'setIsShowPlayList', value: value });
            }
        },
        myCollect: {
            get: function () {
                return this.$store.state.myCollect;
            },
            set: function (value) {
                this.$store.commit({ type: 'setMyCollect', value: value });
            }
        },
        playingYinGuiObj: {
            get: function () {
                return this.$store.state.playingYinGuiObj;
            },
            set: function (value) {
                this.$store.commit({ type: 'setPlayingYinGuiObj', value: value });
            }
        },
        isCollection: {
            get: function () {
                let bool = this.myCollect.some((v) => {
                    return v.albumId == this.playingYinGuiObj.albumId;
                });
                return bool;
            }
        }
    },
    mounted: function () {
        this.controlsParams.isShowControls = false;
    }
}
</script>

<style lang="less">
.SoundView {
    font-size: 12px;
    position: relative;
    background-color: #f1f3f4;

    .notify {
        position: fixed;
        top: 10rem;
        left: 50%;
        width: 50%;
        height: 36rem;
        border-radius: 0 10rem;
        overflow: hidden;
        transform: translateX(-50%);
    }

    .content {
        background-color: #fa2a00e9;
        border-radius: 0 0 10rem 10rem;
        padding: 10rem 0;

        >img {
            display: block;
            width: 80%;
            margin: 0 auto;
            border-radius: 10px;
        }

        >.title {
            width: 80%;
            font-size: 16px;
            color: #fff;
            font-weight: bold;
            margin: 20rem auto;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            text-align: center;
        }

        >.controls {
            >.bar {
                display: flex;
                justify-content: center;
                align-items: center;

                >.icon {
                    width: 15rem;
                    height: 15rem;
                }

                >.slider {
                    width: 70%;
                    margin: 0 20rem;

                }
            }

            >.time {
                width: 70%;
                margin: 0 auto;
                display: flex;
                justify-content: space-between;
                color: #fff;
            }

            >.btns {
                display: flex;
                justify-content: space-evenly;
                align-items: center;
                margin: 15rem 0;

                >.icon {
                    width: 30rem;
                    height: 30rem;
                }

                >.isCollect {
                    .icon {
                        width: 25rem;
                        height: $width;
                    }
                }

                >.isPlay {
                    >.icon {
                        width: 45rem;
                        height: 45rem;
                    }
                }
            }
        }
    }
}
</style>