<template>
    <div class="TingView">
        <van-list v-model="loading" :finished="finished" finished-text="" @load="onLoad">
            <TingdanItem v-for="(item,index) in tingDans" :tingDan="item" :key="index"></TingdanItem>
        </van-list>
    </div>
</template>

<script>
const axios = require('axios').default;
import TingdanItem from '@/components/TingdanItem.vue';
export default {
    name: 'TingView',
    data: function () {
        return {
            tingDans: [],
            pageNum: 1,
            loading: false,
            finished: false,
        };
    },
    methods: {
        onLoad() {
            // 
            axios.get(`https://apis.netstart.cn/ximalaya/ting/categorySubjectList/${this.$route.params.module}`, { params: { pageNum: ++this.pageNum, pageSize: 5 } })
                .then((data) => {
                    let subjects = data.data.data.subjects;
                    subjects.forEach((v) => {
                        this.tingDans.push({
                            id: v.id,
                            authorId: v.authorId,
                            coverPathSmall: v.coverPathSmall,
                            title: v.title,
                            contentType: v.contentType,
                            dataCount: v.dataCount,
                            releaseAt: v.releaseAt
                        });
                    });

                    // 
                    this.loading = false;

                    // 
                    if (subjects.length == 0) {
                        this.finished = true;
                    }
                });
        }
    },
    components: {
        TingdanItem
    },
    created: function () {
        axios.get(`https://apis.netstart.cn/ximalaya/ting/categorySubjectList/${this.$route.params.module}`, { params: { pageNum: 1, pageSize: 10 } })
            .then((data) => {
                let subjects = data.data.data.subjects;
                subjects.forEach((v) => {
                    this.tingDans.push({
                        id: v.id,
                        authorId: v.authorId,
                        coverPathSmall: v.coverPathSmall,
                        title: v.title,
                        contentType: v.contentType,
                        dataCount: v.dataCount,
                        releaseAt: v.releaseAt
                    });
                });
            });
    },
    watch: {
        $route: {
            handler: function (to) {
                this.tingDans = [];
                this.finished = false;
                axios.get(`https://apis.netstart.cn/ximalaya/ting/categorySubjectList/${to.params.module}`, { params: { pageNum: 1, pageSize: 10 } })
                    .then((data) => {
                        let subjects = data.data.data.subjects;
                        subjects.forEach((v) => {
                            this.tingDans.push({
                                id: v.id,
                                authorId: v.authorId,
                                coverPathSmall: v.coverPathSmall,
                                title: v.title,
                                contentType: v.contentType,
                                dataCount: v.dataCount,
                                releaseAt: v.releaseAt
                            });
                        });
                    });
            }
        }
    }
}
</script>

<style lang="less">
.TingView {}
</style>