<template>
    <div class="AlbumStyle2Component" @click="AlbumComponentClick(albumMsg.id)">
        <div class="top">
            <div class="cover" :style="{backgroundImage:`url(${getCoverUrl})`}">
            </div>
            <div class="playCount">
                <svg t="1665929676657" class="playIcon" viewBox="0 0 1024 1024" version="1.1"
                    xmlns="http://www.w3.org/2000/svg" p-id="2298" width="128" height="128">
                    <path
                        d="M512 74.666667C270.933333 74.666667 74.666667 270.933333 74.666667 512S270.933333 949.333333 512 949.333333 949.333333 753.066667 949.333333 512 753.066667 74.666667 512 74.666667z m0 810.666666c-204.8 0-373.333333-168.533333-373.333333-373.333333S307.2 138.666667 512 138.666667 885.333333 307.2 885.333333 512 716.8 885.333333 512 885.333333z"
                        p-id="2299" fill="#ffffff"></path>
                    <path
                        d="M708.266667 465.066667l-234.666667-134.4c-8.533333-4.266667-17.066667-6.4-25.6-6.4-29.866667 0-53.333333 23.466667-53.333333 53.333333v268.8c0 8.533333 2.133333 19.2 6.4 25.6 10.666667 17.066667 27.733333 27.733333 46.933333 27.733333 8.533333 0 17.066667-2.133333 25.6-6.4l234.666667-134.4c8.533333-4.266667 14.933333-10.666667 19.2-19.2 6.4-12.8 8.533333-27.733333 4.266666-40.533333-2.133333-14.933333-10.666667-25.6-23.466666-34.133333z m-249.6 162.133333V396.8L661.333333 512l-202.666666 115.2z"
                        p-id="2300" fill="#ffffff"></path>
                </svg>
                <span>{{parseInt(albumMsg.backNum/10000)+''}}</span>
            </div>
        </div>
        <div class="buttom">
            <p>{{albumMsg.topName}}</p>
        </div>
    </div>
</template>

<script>
const lodash = require('lodash');

export default {
    name: 'AlbumStyle2Component',
    props: { albumMsg: Object },
    computed: {
        chooseAlbum: {
            set: function (value) {
                this.$store.commit({ type: 'setChooseAlbum', value: value });
            },
            get: function () {
                return this.$store.state.chooseAlbum;
            }
        },
        getCoverUrl: {
            get: function () {
                if (this.albumMsg.coverUrl.includes("http")) {
                    return this.albumMsg.coverUrl;
                } else {
                    return "http://imagev2.xmcdn.com/" + this.albumMsg.coverUrl;
                }
            }
        }
    },
    methods: {
        AlbumComponentClick: function (id) {
            if (this.$route.fullPath != `/AlbumView?id=${id}`) {
                this.$router.push({ path: '/AlbumView', query: { id: id } });
            }
            let copyAlbumMsg = lodash.cloneDeep(this.albumMsg);
            this.chooseAlbum = copyAlbumMsg;
            localStorage.setItem('chooseAlbum', JSON.stringify(this.chooseAlbum));
        }
    }
}
</script>

<style lang="less">
.AlbumStyle2Component {
    width: 100rem;

    >.top {
        position: relative;
        margin: 5rem 0;

        >.cover {
            display: block;
            width: 100rem;
            height: 100rem;
            border-radius: 5px;
            background-repeat: no-repeat;
            background-size: cover;
        }

        >.playCount {
            position: absolute;
            z-index: 1;
            bottom: 2rem;
            left: 2rem;
            display: flex;
            align-items: center;
            color: #fff;

            >.playIcon {
                width: 15rem;
                height: 15rem;
                margin-right: 2rem;
            }
        }
    }

    >.buttom {
        p {
            width: 90%;
            margin: 0 auto;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            font-size: 14px;
        }
    }
}
</style>