    </div>
</template>

<script>
const moment = require('moment');

export default {
    name: 'TingdanItem',
    props: {
        tingDan: Object
    },
    computed: {
        releaseAt: {
            get: function () {
                return moment(new Date(this.tingDan.releaseAt)).format('YYYY-MM-DD') + " ";
            }
        }
    },
    methods: {
        itemClick: function () {
            this.$router.push({
                path: '/TingDanContent', query: {
                    id: this.tingDan.id,
                    contentType: this.tingDan.contentType
                }
            });
        }
    }
};
</script>

<style lang="less">
.TingdanItem {
    display: flex;
    justify-content: space-around;
    border-bottom: 3px solid #f9fafa;
    padding: 10rem 0;

    >.left {
        img {
            display: block;
            width: 70rem;
            height: $width;
        }
    }

    >.right {
        width: 75%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;

        >.title {
            font-size: 18px;
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
        }

        >.buttom {
            display: flex;
            align-items: center;
            margin-top: 5rem;
            font-size: 12px;

            >.num {
                margin: 0 8rem 0 4rem;
            }

            >.icon {
                width: 13rem;
                height: 13rem;
            }
        }
    }
}
</style>