<template>
    <div class="YinGuiItemComponent" @click="YinGuiClick(YinGuiItem.id)">
        <div class="left">
            <img src="../../public/img/GMCoOSMF-28lAAAljwEkktf6.gif" v-if="controlsParams.id==YinGuiItem.id">
            <svg t="1665890694776" v-else class="playIcon icon" viewBox="0 0 1024 1024" version="1.1"
                xmlns="http://www.w3.org/2000/svg" p-id="2275" width="128" height="128">
                <path
                    d="M512 0C230.4 0 0 230.4 0 512s230.4 512 512 512 512-230.4 512-512S793.6 0 512 0z m0 981.333333C253.866667 981.333333 42.666667 770.133333 42.666667 512S253.866667 42.666667 512 42.666667s469.333333 211.2 469.333333 469.333333-211.2 469.333333-469.333333 469.333333z"
                    p-id="2276" fill="#2c2c2c"></path>
                <path
                    d="M672 441.6l-170.666667-113.066667c-57.6-38.4-106.666667-12.8-106.666666 57.6v256c0 70.4 46.933333 96 106.666666 57.6l170.666667-113.066666c57.6-42.666667 57.6-106.666667 0-145.066667z"
                    p-id="2277" fill="#2c2c2c"></path>
            </svg>
        </div>
        <div class="right">
            <p class="title">
                {{YinGuiItem.title}}
            </p>
        </div>
    </div>
</template>

<script>
const lodash = require('lodash');

export default {
    name: 'YinGuiItemComponent',
    props: { YinGuiItem: Object },
    computed: {
        controlsParams: {
            get: function () {
                return this.$store.state.controlsParams;
            },
            set: function (value) {
                this.$store.commit({ type: 'setControlsParams', value: value });
            }
        },
        lookingYinGuiObj: {
            get: function () {
                return this.$store.state.lookingYinGuiObj;
            },
            set: function (value) {
                this.$store.commit({ type: 'setLookingYinGuiObj', value: value });
            }
        },
        playingYinGuiObj: {
            get: function () {
                return this.$store.state.playingYinGuiObj;
            },
            set: function (value) {
                this.$store.commit({ type: 'setPlayingYinGuiObj', value: value });
            }
        }
    },
    methods: {
        YinGuiClick: function (id) {
            if (this.controlsParams.id != id) {
                let copyControlsParams = lodash.cloneDeep(this.controlsParams);
                copyControlsParams.id = this.YinGuiItem.id;
                copyControlsParams.isPaly = true;
                copyControlsParams.rate = this.YinGuiItem.duration;
                copyControlsParams.palyPath = this.YinGuiItem.playPath;
                copyControlsParams.title = this.YinGuiItem.title;
                copyControlsParams.cover = this.YinGuiItem.cover;

                this.controlsParams = copyControlsParams;
                localStorage.setItem('controlsParams', JSON.stringify(this.controlsParams));

                let copyLookingYinGuiObj = lodash.cloneDeep(this.lookingYinGuiObj);
                this.playingYinGuiObj = copyLookingYinGuiObj;
                localStorage.setItem('playingYinGuiObj', JSON.stringify(this.playingYinGuiObj));

                let audio = document.querySelector('#app>audio');
                setTimeout(() => {
                    audio.currentTime = this.YinGuiItem.currentRate;
                    audio.play();
                }, 500);
            }
        },
    }

};
</script>

<style lang="less">
.YinGuiItemComponent {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 20rem 0;
    border-bottom: 3px solid #f9fafa;

    >.left {

        >.icon {
            width: 30rem;
            height: 30rem;
        }

        >img {
            width: 30rem;
        }
    }

    >.right {
        width: 80%;

        >.title {
            width: 100%;
            overflow: hidden;
            text-overflow: ellipsis;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            font-size: 12px;
        }
    }
}
</style>