<template>
  <div class="HistoryItemComponent" @click="itemClick">
    <div class="left">
      <img :src="getCover">
    </div>
    <div class="right">
      <p class="albumTitle">
        {{historyItem.albumTitle}}
      </p>
      <p class="yinGuiTitle">{{getYinGui?getYinGui.title:""}}</p>
      <p class="playingTime">
        <span class="time">
          <svg t="1666750493234" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
            p-id="2443" width="128" height="128">
            <path
              d="M511.913993 63.989249c-247.012263 0-447.924744 200.912481-447.924744 447.924744s200.912481 447.924744 447.924744 447.924744 447.924744-200.912481 447.924744-447.924744S758.926256 63.989249 511.913993 63.989249zM511.913993 895.677474c-211.577356 0-383.763481-172.186125-383.763481-383.763481 0-211.577356 172.014111-383.763481 383.763481-383.763481s383.763481 172.014111 383.763481 383.763481S723.491349 895.677474 511.913993 895.677474z"
              p-id="2444" fill="#bfbfbf"></path>
            <path
              d="M672.05913 511.913993l-159.973123 0L512.086007 288.123635c0-17.717453-14.277171-32.166639-31.994625-32.166639-17.717453 0-31.994625 14.449185-31.994625 32.166639l0 255.956996c0 17.717453 14.277171 31.994625 31.994625 31.994625l191.967747 0c17.717453 0 32.166639-14.277171 32.166639-31.994625C704.053754 526.191164 689.604569 511.913993 672.05913 511.913993z"
              p-id="2445" fill="#bfbfbf"></path>
          </svg>
          <span>{{getTime}}</span>
        </span>
        <span class="yibo">{{getYiBo}}</span>
      </p>
    </div>
  </div>
</template>

<script>
const lodash = require('lodash');
export default {
  name: 'HistoryItemComponent',
  props: { historyItem: Object },
  methods: {
    itemClick: function () {
      let playAlbumsHistory = this.playAlbumsHistory;
      let newArr = playAlbumsHistory.filter((v) => {
        return v.albumId != this.historyItem.albumId;
      });
      newArr.unshift(lodash.cloneDeep(this.historyItem));
      this.playAlbumsHistory = newArr;

      let controlsParams = lodash.cloneDeep(this.controlsParams);
      let getYinGui = this.getYinGui;
      if (getYinGui) {
        // id
        controlsParams.id = getYinGui.id;
        // 
        controlsParams.title = getYinGui.title;
        // 
        controlsParams.palyPath = getYinGui.playPath;
        // 
        controlsParams.cover = getYinGui.cover;
        // 
        controlsParams.rate = getYinGui.duration;
      }
      // 
      controlsParams.isPaly = true;
      this.controlsParams = controlsParams;
      localStorage.setItem('controlsParams', JSON.stringify(this.controlsParams));

      this.playingYinGuiObj = lodash.cloneDeep(this.historyItem);
      localStorage.setItem('playingYinGuiObj', JSON.stringify(this.playingYinGuiObj));


      let time = 0;
      if (getYinGui) {
        time = getYinGui.currentRate;
      }
      let audio = document.querySelector('#app>audio');
      let t = setTimeout(() => {
        audio.currentTime = time;
        audio.play();
        clearTimeout(t);
      }, 500);
    }
  },
  computed: {
    getCover: {
      get: function () {
        let cover = this.historyItem.albumCover;
        return cover.includes('http') ? cover : 'http://imagev2.xmcdn.com/' + cover;
      }
    },
    getYinGui: {
      get: function () {
        let YinGuiList = this.historyItem.YinGuiList;
        let albums = YinGuiList.filter((v) => {
          return v.currentRate != 0;
        });
        albums.reverse();
        return albums[0];
      }
    },
    getTime: {
      get: function () {
        let duration = 0;
        if (this.getYinGui) {
          duration = this.getYinGui.duration;
        }
        let mNum = duration / 60;
        let mm = parseInt(mNum) < 10 ? '0' + parseInt(mNum) : parseInt(mNum);
        let sNum = (mNum - parseInt(mNum)) * 60;
        let ss = parseInt(sNum) < 10 ? '0' + parseInt(sNum) : parseInt(sNum);
        return mm + ':' + ss;
      }
    },
    getYiBo: {
      get: function () {
        let yibo = 0;
        if (this.getYinGui) {
          yibo = this.getYinGui.currentRate / this.getYinGui.duration * 100;
        }
        return parseInt(yibo) + '%';
      }
    },
    playAlbumsHistory: {
      get: function () {
        return this.$store.state.playAlbumsHistory;
      },
      set: function (value) {
        this.$store.commit({ type: 'setPlayAlbumsHistory', value: value });
      }
    },
    playingYinGuiObj: {
      get: function () {
        return this.$store.state.playingYinGuiObj;
      },
      set: function (value) {
        this.$store.commit({ type: 'setPlayingYinGuiObj', value: value });
      }
    },
    controlsParams: {
      get: function () {
        return this.$store.state.controlsParams;
      },
      set: function (value) {
        this.$store.commit({ type: 'setControlsParams', value: value });
      }
    }
  }
}
</script>

<style lang="less">
.HistoryItemComponent {
  display: flex;
  align-items: center;
  padding: 10rem 0;
  border: 1px solid #f1f3f4;

  >.left {
    width: 25%;
    display: flex;
    justify-content: center;

    img {
      display: block;
      width: 60rem;
      border-radius: 5rem;
    }
  }

  >.right {
    width: 75%;

    >.albumTitle {
      font-size: 14px;
    }

    >.yinGuiTitle {
      color: gray;
      margin: 5rem 0;
    }

    >.playingTime {
      display: flex;
      align-items: center;

      .yibo {
        color: burlywood;
      }

      .time {
        display: flex;
        align-items: center;
        margin-right: 5rem;

        >.icon {
          width: 15rem;
          height: 15rem;
          margin-right: 5rem;
        }
      }
    }

    >.albumTitle,
    >.yinGuiTitle {
      width: 80%;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
</style>