<template>
    <div class="TomatosComponent">
        <section class="tomatoes">
            <div class="item" @click="itemClick(item.link)" v-for=" (item,index) in tomatoes" :key="index">
                <img :src="item.img">
                <p>{{item.name}}</p>
            </div>
        </section>
    </div>
</template>

<script>
export default {
    name: 'TomatosComponent',
    props: {
        tomatoes: Array
    },
    methods: {
        itemClick: function (link) {
            this.$router.push({ path: link });
        }
    }
}
</script>

<style lang="less">
.TomatosComponent {
    >.tomatoes {
        padding: 20rem 0;
        display: flex;
        justify-content: space-evenly;

        >.item {
            width: 60rem;
            display: flex;
            flex-direction: column;
            align-items: center;

            >img {
                display: block;
                width: 60%;
                border-radius: 50%;
            }

            >p {
                font-size: 12px;
            }
        }
    }
}
</style>