          <van-tabs v-model="active" :animated="true" color="#ff4613">
            <van-tab title="">
              <van-list v-model="loading" :style="{height:'540rem',overflow:'auto'}" :finished="finished"
                finished-text="" @load="onLoad">
                <PlayListYinGuiItem v-for="(item,index) in playingYinGuiObj.YinGuiList" :YinGuiMsg="item" :key="index">
                </PlayListYinGuiItem>
              </van-list>
            </van-tab>
            <van-tab title="">
              <section :style="{maxHeight:'540rem',overflow:'auto'}">
                <HistoryItemComponent v-for="(item,index) in myCollect" :historyItem="item" :key="index">
                </HistoryItemComponent>
              </section>
            </van-tab>
            <van-tab title="">
              <section :style="{maxHeight:'540rem',overflow:'auto'}">
                <HistoryItemComponent v-for="(item,index) in playAlbumsHistory" :historyItem="item" :key="index">
                </HistoryItemComponent>
