<script>
import PlayListYinGuiItem from '@/components/PlayListYinGuiItem.vue';
import HistoryItemComponent from '@/components/HistoryItemComponent.vue';
import axios from 'axios';
const lodash = require('lodash');
export default {
  name: 'App',
  data: function () {
    return {
      active: 0,
      // list
      loading: false,
      finished: false
    };
  },
  methods: {
    isPlayClick: function () {
      let controlsParams = this.controlsParams;
      controlsParams.isPaly = !controlsParams.isPaly;
      this.controlsParams = controlsParams;
      localStorage.setItem('controlsParams', JSON.stringify(this.controlsParams));

      let audio = document.querySelector('#app>audio');
      if (this.controlsParams.isPaly) {
        setTimeout(() => {
          if (this.controlsParams.currentRate == 0) {
            audio.play();
          } else {
            audio.currentTime = this.controlsParams.currentRate;
            audio.play();
          }
        }, 500);
      } else {
        audio.pause();
      }
    },
    audioTimeupdate: function () {
      let audio = document.querySelector('#app>audio');
      let copyControlsParams = lodash.cloneDeep(this.controlsParams);
      copyControlsParams.currentRate = audio.currentTime;
      this.controlsParams = copyControlsParams;
      localStorage.setItem('controlsParams', JSON.stringify(this.controlsParams));
    },
    palyListClick: function () {
      this.isShowPlayList = !this.isShowPlayList;
    },
    onLoad() {
      // 
      axios.get('https://apis.netstart.cn/ximalaya/album/queryAlbumTrackRecordsByPage',
        {
          params: {
            albumId: this.playingYinGuiObj.albumId,
            page: ++this.playingYinGuiObj.nowYinGuiPage,
            pageSize: 10,
            asc: true,
            countKeys: 'play,comment'
          }
        })
        .then((data) => {
          let trackDetailInfos = data.data.data.trackDetailInfos;
          let playingYinGuiObj = lodash.cloneDeep(this.playingYinGuiObj);
          trackDetailInfos.forEach((v) => {
            // 
            playingYinGuiObj.YinGuiList.push({
              id: v.id,
              title: v.trackInfo.title,
              cover: v.trackInfo.cover,
              duration: v.trackInfo.duration,
              playPath: v.trackInfo.playPath,
              currentRate: 0
            });
          });
          this.playingYinGuiObj = playingYinGuiObj;
          this.loading = false;

          if (playingYinGuiObj.YinGuiList.length >= playingYinGuiObj.maxYinGuiNum) {
            this.finished = true;
          }
        });
    },
    audioControlsClick: function () {
      this.$router.push({ path: '/SoundView' });
    }
  },
  computed: {
    controlsParams: {
      get: function () {
        return this.$store.state.controlsParams;
      },
      set: function (value) {
        this.$store.commit({ type: 'setControlsParams', value: value });
      }
    },
    getCover: {
      get: function () {
        let cover = this.controlsParams.cover;
        return cover.includes('http') ? cover : 'http://imagev2.xmcdn.com/' + cover;
      }
    },
    currentTime: {
      get: function () {
        return this.controlsParams.currentRate / this.controlsParams.rate * 100;
      },
      set: function () {
        this.currentRate = this.controlsParams.currentRate / this.controlsParams.rate * 100;
      }
    },
    playingYinGuiObj: {
      get: function () {
        return this.$store.state.playingYinGuiObj;
      },
      set: function (value) {
        this.$store.commit({ type: 'setPlayingYinGuiObj', value: value });
      }
    },
    playAlbumsHistory: {
      get: function () {
        return this.$store.state.playAlbumsHistory;
      },
      set: function (value) {
        this.$store.commit({ type: 'setPlayAlbumsHistory', value: value });
      }
    },
    lookingYinGuiObj: {
      get: function () {
        return this.$store.state.lookingYinGuiObj;
      },
      set: function (value) {
        this.$store.commit({ type: 'setLookingYinGuiObj', value: value });
      }
    },
    isShowPlayList: {
      get: function () {
        return this.$store.state.isShowPlayList;
      },
      set: function (value) {
        this.$store.commit({ type: 'setIsShowPlayList', value: value });
      }
    },
    myCollect: {
      get: function () {
        return this.$store.state.myCollect;
      },
      set: function (value) {
        this.$store.commit({ type: 'setMyCollect', value: value });
      }
    }
  },
  mounted: function () {
    let copyControlsParams = lodash.cloneDeep(this.controlsParams);
    copyControlsParams.isPaly = false;
    this.controlsParams = copyControlsParams;
  },
  watch: {
    'controlsParams.currentRate': {
      handler: function (newValue) {
        let lookingYinGuiObj = this.lookingYinGuiObj;
        let LYinGuiList = lookingYinGuiObj.YinGuiList;
        for (let i = 0; i <= LYinGuiList.length - 1; i++) {
          if (LYinGuiList[i].id == this.controlsParams.id) {
            LYinGuiList[i].currentRate = newValue;
            break;
          }
        }

        let playingYinGuiObj = this.playingYinGuiObj;
        let PYinGuiList = playingYinGuiObj.YinGuiList;
        for (let i = 0; i <= PYinGuiList.length - 1; i++) {
          if (PYinGuiList[i].id == this.controlsParams.id) {
            PYinGuiList[i].currentRate = newValue;
            localStorage.setItem('playingYinGuiObj', JSON.stringify(playingYinGuiObj));
            break;
          }
        }

        let playAlbumsHistory = this.playAlbumsHistory;
        let index;
        let isHave = playAlbumsHistory.some((v, i) => {
          index = i;
          return v.albumId == playingYinGuiObj.albumId;
        });
        if (!isHave) {
          playAlbumsHistory.unshift(lodash.cloneDeep(playingYinGuiObj));
        } else {
          playAlbumsHistory.splice(index, 1);
          playAlbumsHistory.unshift(lodash.cloneDeep(playingYinGuiObj));
        }
        localStorage.setItem('playAlbumsHistory', JSON.stringify(playAlbumsHistory));
      }
    }
  },
  components: {
    PlayListYinGuiItem,
    HistoryItemComponent
  }
}
</script>

