<template>
    <div class="playListYinGuiItem" @click="YinGuiItemClick">
        <div class="left">
            <img src="../../public/img/GMCoOSMF-28lAAAljwEkktf6.gif" v-if="controlsParams.id==YinGuiMsg.id">
            <p class="title" :class="{active:controlsParams.id==YinGuiMsg.id}">{{YinGuiMsg.title}}</p>
        </div>
        <div class="right" v-if="jinDu!=0">
             {{jinDu+'%'}}
        </div>
    </div>
</template>

<script>
const lodash = require('lodash');
export default {
    name: 'playListYinGuiItem',
    props: { YinGuiMsg: Object },
    computed: {
        jinDu: {
            get: function () {
                let result = this.YinGuiMsg.currentRate / this.YinGuiMsg.duration * 100;
                return parseInt(result);
            }
        },
        controlsParams: {
            get: function () {
                return this.$store.state.controlsParams;
            },
            set: function (value) {
                this.$store.commit({ type: 'setControlsParams', value: value });
            }
        }
    },
    methods: {
        YinGuiItemClick: function () {
            if (this.controlsParams.id != this.YinGuiMsg.id) {
                let copyControlsParams = lodash.cloneDeep(this.controlsParams);
                copyControlsParams.id = this.YinGuiMsg.id;
                copyControlsParams.isPaly = true;
                copyControlsParams.rate = this.YinGuiMsg.duration;
                copyControlsParams.palyPath = this.YinGuiMsg.playPath;
                copyControlsParams.title = this.YinGuiMsg.title;
                copyControlsParams.cover = this.YinGuiMsg.cover;

                this.controlsParams = copyControlsParams;
                localStorage.setItem('controlsParams', JSON.stringify(this.controlsParams));

                let time = this.YinGuiMsg.currentRate;
                let audio = document.querySelector('#app>audio');
                let t = setTimeout(() => {
                    audio.currentTime = time;
                    audio.play();
                    clearTimeout(t);
                }, 500);
            }
        }
    }
};
</script>

<style lang="less">
.playListYinGuiItem {
    font-size: 12px;
    display: flex;
    align-items: center;
    padding: 14rem 0;
    border: 1rem solid #f1f3f4;

    .left {
        width: 80%;
        display: flex;
        align-items: center;
        justify-content: space-around;

        >img {
            display: block;
            width: 8%;
        }

        >.title {
            width: 80%;
            overflow: hidden;
            white-space: nowrap;
            text-overflow: ellipsis;
            font-size: 14px;
        }

        >.active {
            color: red;
        }
    }

    .right {
        width: 20%;
        text-align: center;
        color: burlywood;
    }
}
</style>