<template>
    <div class="HomeView">
        <header>
            <img class="logoIcon" @click="backHome" src="../../../public/img/FM-01.png">
            <section class="center">
                <svg t="1665801327510" class="searchIcon" viewBox="0 0 1024 1024" version="1.1"
                    xmlns="http://www.w3.org/2000/svg" p-id="2471" width="128" height="128">
                    <path
                        d="M474.453333 884.053333c-225.28 0-409.6-184.32-409.6-409.6s184.32-409.6 409.6-409.6 409.6 184.32 409.6 409.6-184.32 409.6-409.6 409.6z m0-68.266666c187.733333 0 341.333333-153.6 341.333334-341.333334s-153.6-341.333333-341.333334-341.333333-341.333333 153.6-341.333333 341.333333 153.6 341.333333 341.333333 341.333334z m252.586667 54.613333c-13.653333-13.653333-10.24-37.546667 3.413333-47.786667s37.546667-10.24 47.786667 3.413334l64.853333 78.506666c13.653333 13.653333 10.24 37.546667-3.413333 47.786667s-37.546667 10.24-47.786667-3.413333l-64.853333-78.506667z"
                        p-id="2472" fill="#aeafaf"></path>
                </svg>
                <input type="text">
            </section>
            <button>APP</button>
        </header>
        <section class="topSection">
            <ul class="topBar" v-if="!this.$route.path.includes('ting')">
                <li @click="liModuleClick(item)" :class="{active:item.moduleName==chooseTobModule.moduleName}"
                    v-for="(item,index) in tobModules" :key="index">{{item.moduleName}}</li>
            </ul>
            <section class="tingDan" v-if="this.$route.path.includes('ting')">
                <ul class="tingFenlei" v-if="!isOpen">
                    <li v-for="(item,index) in tingModules" @click="tingFenleiLiClick(item)"
                        :class="{active:item.name==chooseTing.name}" :key="index">{{item.title}}</li>
                </ul>
                <p v-else></p>
                <div class="btn" @click="isOpen=!isOpen;">
                    <svg t="1666185278269" class="downIcon icon" v-if="isOpen" viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="1790" width="128" height="128">
                        <path
                            d="M904 692c0 8.189-3.124 16.379-9.372 22.628-12.497 12.496-32.759 12.496-45.256 0L512 377.255 174.628 714.628c-12.497 12.496-32.758 12.496-45.255 0-12.497-12.498-12.497-32.758 0-45.256l360-360c12.497-12.496 32.758-12.496 45.255 0l360 360C900.876 675.621 904 683.811 904 692z"
                            fill="#ff4a17" p-id="1791"></path>
                    </svg>
                    <svg t="1666185367707" class="upIcon icon" v-else viewBox="0 0 1024 1024" version="1.1"
                        xmlns="http://www.w3.org/2000/svg" p-id="1996" width="128" height="128">
                        <path
                            d="M904 332c0-8.189-3.124-16.379-9.372-22.628-12.497-12.496-32.759-12.496-45.256 0L512 646.745 174.628 309.372c-12.497-12.496-32.758-12.496-45.255 0-12.497 12.498-12.497 32.758 0 45.256l360 360c12.497 12.496 32.758 12.496 45.255 0l360-360C900.876 348.379 904 340.189 904 332z"
                            fill="#8a8a8a" p-id="1997"></path>
                    </svg>
                </div>

                <div class="downConnent" v-if="isOpen">
                    <div class="item" v-for="(item,index) in tingModules" @click="tingFenleiLiClickOther(item)"
                        :key="index" :class="{active:item.name==chooseTing.name}">
                        {{item.title}}</div>
                </div>
            </section>
        </section>
        <div class="line"></div>

        <transition name="ani">
            <router-view name="HomeChildExit"></router-view>
        </transition>
    </div>
</template>

<script>
const axios = require('axios').default;
const lodash = require('lodash');
export default {
    name: 'HomeView',
    data: function () {
        return {
            tobModules: [],
            tingModules: [],
            isOpen: false
        };
    },
    computed: {
        chooseTobModule: {
            get: function () {
                return this.$store.state.chooseTobModule;
            },
            set: function (value) {
                this.$store.commit({ type: 'setChooseTobModule', value: value });
            }
        },
        chooseTing: {
            get: function () {
                return this.$store.state.chooseTing;
            },
            set: function (value) {
                this.$store.commit({ type: 'setChooseTing', value: value });
            }
        },
        controlsParams: {
            get: function () {
                return this.$store.state.controlsParams;
            },
            set: function (value) {
                this.$store.commit({ type: 'setControlsParams', value: value });
            }
        }
    },
    methods: {
        // topBar>li
        liModuleClick: function (module) {
            let copyModule = lodash.cloneDeep(module);
            delete copyModule.order;
            this.chooseTobModule = copyModule;
            localStorage.setItem('chooseTobModule', JSON.stringify(this.chooseTobModule));
            if (module.moduleKey == 'tuijian') {
                if (this.$route.path !== '/') {
                    this.$router.push({ path: '/' });
                }
            } else {
                if (this.$route.path !== `/NavModule/${module.moduleKey}`) {
                    this.$router.push({ path: `/NavModule/${module.moduleKey}` });
                }
            }
        },
        // tingFenlei>li
        tingFenleiLiClick: function (module) {
            let copyModule = lodash.cloneDeep(module);
            this.chooseTing = copyModule;
            localStorage.setItem('chooseTing', JSON.stringify(this.chooseTing));
            if (this.$route.path !== `/ting/${module.name}`) {
                this.$router.replace({ path: `/ting/${module.name}` });
            }
        },
        tingFenleiLiClickOther: function (module) {
            let copyModule = lodash.cloneDeep(module);
            this.chooseTing = copyModule;
            localStorage.setItem('chooseTing', JSON.stringify(this.chooseTing));
            if (this.$route.path !== `/ting/${module.name}`) {
                this.$router.replace({ path: `/ting/${module.name}` });
            }
            this.isOpen = false;
        },
        backHome: function () {
            if (this.$route.path != '/') {
                this.$router.push({ path: '/' });
            }
        }
    },
    created: function () {
        axios.get('https://apis.netstart.cn/ximalaya/index/queryIndexTabModule')
            .then((data) => {
                this.tobModules = data.data.data;
                return axios.get('https://apis.netstart.cn/ximalaya/ting/categories');
            })
            .then((data) => {
                this.tingModules = data.data.data.categories;
            });
    },
    mounted: function () {
        this.controlsParams.isShowControls = true;
    }
}
</script>

<style lang="less">
.HomeView {
    position: relative;

    >header {
        width: 100%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        padding: 20rem 0;

        >.logoIcon {
            display: block;
            width: 70rem;
            height: 15rem;
        }

        >.center {
            position: relative;
            display: flex;
            align-items: center;

            >.searchIcon {
                position: absolute;
                width: 14rem;
                height: 14rem;
                margin-left: 7rem;
            }

            >input {
                width: 167rem;
                height: 30rem;
                border-radius: 25px;
                background-color: rgb(239, 241, 244);
                border: none;
                text-indent: 25rem;
            }
        }

        >button {
            border: 1px solid #fa340e;
            padding: 5rem 10rem;
            color: #fa340e;
            background-color: #fff;
            border-radius: 15px;
            font-size: 12px;
        }

    }

    >.topSection {
        width: 100%;
        top: 70rem;
        display: flex;

        >.topBar,
        >.tingDan .tingFenlei {
            width: 100%;
            list-style: none;
            display: flex;
            overflow: auto;

            >li {
                margin: 10rem;
                font-size: 16px;
                white-space: nowrap;
                position: relative;
                color: gray;
            }

            >.active::after {
                display: block;
                content: "";
                width: 70%;
                height: 2px;
                background-color: #fa340e;
                position: absolute;
                bottom: -5px;
                left: 50%;
                transform: translateX(-50%);
                border-radius: 25px;
            }

            >.active {
                font-size: 20px;
                color: black;
            }

            &::-webkit-scrollbar {
                display: none;
            }
        }

        >.tingDan {
            width: 100%;
            display: flex;
            position: relative;

            >.tingFenlei,
            >p {
                width: 80%;
            }

            >p {
                height: 48rem;
                line-height: $height;
                font-size: 14px;
                color: rgba(128, 128, 128, 0.559);
                padding: 0 10rem;
            }

            >.btn {
                width: 20%;
                display: flex;
                justify-content: center;
                align-items: center;

                >.icon {
                    width: 20rem;
                    height: $width;
                }
            }

            >.downConnent {
                position: absolute;
                top: 48rem;
                height: 340rem;
                width: 100%;
                overflow: auto;
                display: flex;
                flex-wrap: wrap;
                justify-content: space-evenly;
                background-color: #fff;
                padding: 5rem 0;

                >.item {
                    width: 30%;
                    background-color: #f3f4f5;
                    border-radius: 5px;
                    text-align: center;
                    padding: 7rem 0;
                    font-size: 14px;
                    margin: 5rem 0;
                }

                >.active {
                    color: #fff;
                    background-color: #fa340e;
                }
            }
        }

    }

    >header,
    >.topSection {
        position: fixed;
        z-index: 2;
        background-color: #fff;
    }

    >.line {
        &::before {
            display: block;
            content: "";
            height: 120rem;
        }
    }

    /*
  
  
*/
    .ani-leave-active,
    .ani-enter-active {
        transition: all 0.5s linear;
    }

    .ani-enter,
    .ani-leave-to {
        opacity: 0;
    }

    .ani-enter-to,
    .ani-leave {
        opacity: 1;
    }
}
</style>